import styled from 'styled-components';
import { Link } from 'react-router-dom';
import {FaTimes} from 'react-icons/fa';

export const SidebarContainer = styled.aside`
  position: fixed;
  Z-index: 999;
  width: 350px;
  height: 100%;
  background: #ffc500;
  display: grid;
  align-items: center;
  top: 0;
  transition: 0.3s ease-in-out;
  right: ${({ isOpen }) => (isOpen ? '0' : '-1000px')};

  @media screen and (max-width: 400px) {
      width: 100%;
  }

`;

export const CloseIcon = styled(FaTimes)`
  color: #000;

`;

export const Icon = styled.div`
  position: absolute;
  top: 1.2rem;
  right: 1.5rem;
  background: transparent;
  border: transparent;
  font-size: 2rem;
  cursor: pointer;
  outline: none;

`;

export const SidebarMenu = styled.div`
  display: grid;
  gride-template-columns: 1fr;
  gride-template-rows: repeat(3, 80px);

`;

export const SidebarLink = styled(Link)`
 display: flex;
 align-items: center;
 justify-content: center;
 font-size: 1.5rem;
 text-decoration: none;
 list-style: none;
 transition: 0.2rem ease-in-out;
 text-decortion: none;
 color: #000;
 cursor: pointer;



 @hover {
     color: #e31837;
     transition: 0.2s ease-in-out;
 }

`;

 export const SideBtnWrap = styled.div`
   display: flex;
   justify-content: center;
   marginLeft: 25%;
   marginRight: 25%;
 
 `;
  
 export const SidebarRouter = styled( Link )`
    
    background: #e31837;
    justifyContent: center;
    alignItems: "center";
    margin: 1;
    white-space: nowrap;
    padding: 16px 64px;
    color: #fff;
    font-size: 16px;
    outline: none;
    border: none;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    text-decoration: none;


  &:hover {
    transtion: 0.2s ease-in-out;
    background: #fff;
    color: #010606;
    marginRight: 2;

  }  

   
 
 `;
